<template>
  <v-container>
    <v-row>
      <v-col>
        <v-alert class="subtitle-1" color="cyan" border="left" outlined dense icon="mdi-twitter">
          {{this.$route.name}} 페이지 제목을 표시
        </v-alert>
      </v-col>
    </v-row>
    <v-row>
      <v-col class="d-flex justify-end py-0">
        <v-btn  @click=" fetchData" text>버튼 1</v-btn>
        <v-btn  @click=" fetchData" text>버튼 2</v-btn>
        <v-btn  @click=" fetchData" text>버튼 3</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-data-table dense :headers="headers" :items="items" item-key="id"
          class="elevation-1 subtitle-1 blue-grey--text">
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    items: [],
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        filterable: false,
        value: 'id',
      },
      { text: '아이디', value: 'username' },
      { text: '웹사이트', value: 'website' },
      { text: '이메일', value: 'email' },
      { text: '이름', value: 'name' },
      { text: '전화번호', value: 'phone' },
    ],

  }),
  created() {
    this.fetchData()

  },
  methods: {
    fetchData: function () {
      var vm = this;
      this.$axios.get('https://jsonplaceholder.typicode.com/users/')
        .then(function (response) {
          vm.items = response.data 
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  }
}
</script>

